import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import {
  Form,
  Input,
  Col,
  Modal,
  Select,
  Radio,
  Divider,
  Checkbox
} from "antd";

const { Option } = Select;
const InputGroup = Input.Group;

class ModalAddAdjustmentType extends Component {
  state = {
    check_code: false
  };

  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.adjustmentType;
    if (success) {
      resetFields();
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["adjustment_type_code"]: null
      });
    }
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { adjustmentType, issueReceipt } = this.props;
    const loading = adjustmentType.loading;
    const { accounts } = issueReceipt;
    const error = adjustmentType.errorField ? adjustmentType.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "50%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <Modal
        title={<IntlMessages id="adjustmentTypes.add.title" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <InputGroup compact>
              <Col span={20}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="adjustmentTypes.add.code" />}
                  validateStatus={
                    error.adjustment_type_code ||
                    getFieldError("adjustment_type_code")
                      ? "error"
                      : null
                  }
                  help={
                    error.adjustment_type_code
                      ? error.adjustment_type_code
                      : getFieldError("adjustment_type_code")
                  }
                >
                  {getFieldDecorator("adjustment_type_code", {
                    //initialValue: data.adjustment_type_code,
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(<Input disabled={this.state.check_code ? true : false} />)}
                </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{
                      paddingTop: "5px",
                      paddingLeft: "8px"
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="label.description" />}
            >
              {getFieldDecorator("description", {
                //initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="adjustmentTypes.add.costAdjustment" />}
            >
              {getFieldDecorator("cost_adjustment", {
                initialValue: 0
              })(
                <Radio.Group buttonStyle="solid" style={radioGroupStyle}>
                  <Radio.Button style={radioButtonStyle} value={1}>
                    <IntlMessages id="yes" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={0}>
                    <IntlMessages id="no" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="adjustmentTypes.add.itemTransfer" />}
            >
              {getFieldDecorator("item_transfer", {
                initialValue: 0
              })(
                <Radio.Group buttonStyle="solid" style={radioGroupStyle}>
                  <Radio.Button style={radioButtonStyle} value={1}>
                    <IntlMessages id="yes" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={0}>
                    <IntlMessages id="no" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
          </div>

          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="adjustmentTypes.add.subtitle.accounts" />
          </Divider>

          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <Form.Item
              {...formItemLayout}
              label={
                <IntlMessages id="adjustmentTypes.add.adjustmentAccount" />
              }
            >
              {getFieldDecorator("id_account_adjustment", {
                //initialValue: data.id_account_adjustment,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={adjustmentType.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {accounts.map((data, key) => (
                    // data.id_account_type === "13" ||
                    // data.id_account_type === "15" ||
                    // data.id_account_type === "16" ||
                    // data.id_account_type === "17" ||
                    // data.id_account_type === "18"
                    // ?
                    <Option key={key} value={data.id_account}>
                      {data.account_code + " (" + data.account_name + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddAdjustmentType = Form.create({
  name: "modal_add_adjustment_type"
})(ModalAddAdjustmentType);

export default WrappedModalAddAdjustmentType;
