import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import { Form, Input, Col, Row, Modal } from "antd";
import getLanguage from "../../../App/MyLanguange";

class ModalPassword extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  componentDidUpdate(prevProps) {
    if (this.props.visible !== prevProps.visible) {
      this.props.form.setFieldsValue({ user_password: null });
    }
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { loading } = this.props;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="passwordConfirm" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="30%"
        okText={getLanguage({ id: "button.ok" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="users.add.password" />}
              >
                {getFieldDecorator("user_password", {
                  //initialValue: data.user_password,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(<Input.Password onPressEnter={this.handleSubmit} />)}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedPasswordModal = Form.create({ name: "password_modal" })(
  ModalPassword
);

export default WrappedPasswordModal;
