import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import NumberFormat from "../../../App/NumberFormat";
import CardWrapper from "../../../App/app.style";
import ModalItemDetail from "./modalItemDetail";
import {
  getBaseQty,
  convertQty,
  convertPrice,
  getUnitConversion
} from "../../../../helpers/helper";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getTotal = arr => {
  let totalQty = 0;
  let totalAmount = 0;
  arr.map(data => {
    totalQty += parseFloat(data.base_qty);
    totalAmount += parseFloat(data.sub_total);
  });

  return { totalQty: totalQty, totalAmount: totalAmount };
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { items } = props;

    this.state = {
      dataSource: [...items],
      count: items.length + 1,
      totalQty: 0,
      visible: false,
      selectedItem: {}
    };
  }

  componentDidUpdate(prevProps) {
    const {
      loadingItems,
      item,
      items,
      clearItem,
      clearItems,
      handleSaveItemList,
      cost_adjustment,
      clear,
      status
    } = this.props;

    if (!loadingItems && item) {
      const { dataSource } = this.state;
      const { count } = this.state;
      const find = dataSource.findIndex(
        currData => currData.id_item === item.id_item
      );
      // console.log(find);

      if (find < 0) {
        const old_qty = item.current_qty ? parseFloat(item.current_qty) : 0;
        const cost = item.item_cost
          ? parseFloat(item.item_cost)
          : 0;
        let newData = {
          index_no: count,
          id_item: item.id_item,
          item_code: item.item_code,
          item_name: item.item_name,
          old_qty: old_qty,
          base_old_qty: old_qty,
          qty: 1,
          new_qty: old_qty + 1,
          id_unit: item.preference_item.purchase_unit.id_unit,
          unit_code: item.preference_item.purchase_unit.unit_code,
          id_base_unit: item.id_base_unit,
          base_unit_code: item.base_unit.unit_code,
          cost: cost,
          item_base_cost: cost,
          sub_total: cost * 1,
          base_qty: 1,
          unit_conversion: item.unit_conversion,
          unit_conv: getUnitConversion(
            item.preference_item.purchase_unit.id_unit,
            item.unit_conversion
          ),
          id_department: null,
          id_project: null
        };

        newData.base_qty = newData.qty * newData.unit_conv;
        // newData.item_base_cost = cost * newData.base_qty;
        newData.new_qty = newData.base_qty + old_qty;
        newData.cost = cost * newData.unit_conv;
        newData.sub_total = cost * newData.base_qty;

        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1
        });

        clearItem([...dataSource, newData]);
        // console.log(this.props.item);
      } else if (find >= 0) {
        const updateData = dataSource.filter(
          currData => currData.index_no !== count
        );

        let newData = Object.assign({}, updateData[find]);
        newData.qty = parseFloat(newData.qty) + 1;
        updateData.splice(find, 1, {
          ...updateData[find],
          ...newData
        });

        this.setState({
          dataSource: [...updateData]
        });

        clearItem([...updateData]);
        // console.log(this.props.item);
      }
    } else if (!loadingItems && items.length !== 0) {
      let newData = [];
      let newIndex = 0;

      items.map((item, key) => {
        let init = {};
        init.index_no = key + 1;
        init.id_item = item.id_item;
        init.item_code = item.item_code;
        init.item_name = item.item_name;
        init.qty = item.qty ? parseFloat(item.qty) : 1;
        init.base_qty = item.base_qty ? parseFloat(item.base_qty) : 1;
        init.base_old_qty = item.old_qty ? parseFloat(item.old_qty) : 0;
        init.old_qty = init.base_old_qty;

        init.id_unit = item.id_unit;
        init.unit_code = item.unit ? item.unit.unit_code : item.unit_code;
        init.id_base_unit = item.id_base_unit;
        init.base_unit_code = item.base_unit ? item.base_unit.unit_code : item.base_unit_code;
        init.unit_conversion = item.unit_conversion ? item.unit_conversion : item.item.unit_conversion;
        init.unit_conv = getUnitConversion(
          init.id_unit,
          init.unit_conversion
        );

        init.new_qty = init.old_qty + init.qty * init.unit_conv;
        init.cost = item.cost
          ? cost_adjustment
            ? parseFloat(item.cost)
            : status === 1
              ? parseFloat(item.cost) * init.unit_conv
              : parseFloat(item.cost)
          : 0;
        init.item_base_cost = item.item_base_cost ? parseFloat(item.item_base_cost) : 0;

        init.sub_total = init.qty * init.cost;

        init.id_department = item.id_department;
        init.id_project = item.id_project;

        newIndex = init.index_no + 1;
        newData.push(init)
      });

      this.setState({
        dataSource: [...newData],
        count: newIndex
      });

      clearItems(newData);
    } else if (prevProps.cost_adjustment !== cost_adjustment) {
      // if (!cost_adjustment) {
      let newData = this.state.dataSource;
      newData.map(row => {
        row.cost = row.item_base_cost * row.unit_conv;
        row.sub_total = parseFloat(row.qty) * parseFloat(row.cost);
      });
      handleSaveItemList(newData);
      // }
    } else if (clear) {
      this.setState({ dataSource: [], count: 1 });
      handleSaveItemList([]);
    }
  }

  handleItemDetailModal = row => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleItemDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSaveItemList } = this.props;
    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1
    });
    handleSaveItemList(newData);
  };

  handleSave = row => {
    const { dataSource } = this.state;
    const { handleSaveItemList } = this.props;
    const newData = dataSource;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    // row.old_qty = convertQty(
    //   row.base_old_qty,
    //   row.id_unit,
    //   row.unit_conversion
    // );

    // row.cost = convertPrice(
    //   row.cost,
    //   row.unit_conv,
    //   row.id_unit,
    //   row.unit_conversion
    // );
    const new_unit_conv = getUnitConversion(row.id_unit, row.unit_conversion);
    if (row.cost === newData[index].cost) {
      row.cost = row.cost / row.unit_conv * new_unit_conv;
    }
    row.unit_conv = new_unit_conv;
    row.new_qty =
      parseFloat(row.old_qty) +
      parseFloat(row.qty) * parseFloat(row.unit_conv);
    row.sub_total = parseFloat(row.qty) * parseFloat(row.cost);

    row.base_qty = row.qty * row.unit_conv;

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    this.setState({ dataSource: [...newData] });
    handleSaveItemList(newData);
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource, visible, selectedItem } = this.state;
    const { cost_adjustment } = this.props;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: text => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 8,
                align: "right"
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "20%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "30%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="oldQuantity" />}>
            <a href="javascript:;"><IntlMessages id="oldQty" /> </a>
          </Tooltip>
        ),
        dataIndex: "old_qty",
        width: "15%",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <><NumberFormat value={text} /> {row.base_unit_code} </>;
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="adjustmentQuantity" />}>
            <a href="javascript:;"><IntlMessages id="adjustmentQty" /> </a>
          </Tooltip>
        ),
        dataIndex: "qty",
        width: "15%",
        align: "center",
        onCell: row => {
          if (!isNaN(row.index_no)) {
            if (this.props.status === 1) {
              return {
                record: row,
                dataIndex: "qty",
                editable: true,
                handleSave: this.handleSave,
                cost_adjustment: cost_adjustment,
                colSpan: 1
              };
            } else {
              return {
                record: row,
                dataIndex: "qty",
                colSpan: 1
              };
            }
          } else {
            return {
              colSpan: 0
            };
          }
        }
      },
      {
        title: getLanguage({ id: "unit" }),
        width: "10%",
        dataIndex: "unit_code",
        align: "center",
        onCell: row => {
          if (!isNaN(row.index_no)) {
            if (this.props.status === 1) {
              return {
                record: row,
                dataIndex: "unit_code",
                editable: true,
                handleSave: this.handleSave,
                colSpan: 1
              };
            } else {
              return {
                record: row,
                dataIndex: "unit_code",
                colSpan: 1
              };
            }
          } else {
            return {
              colSpan: 0
            };
          }
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="newQuantity" />}>
            <a href="javascript:;"><IntlMessages id="newQty" /> </a>
          </Tooltip>
        ),
        dataIndex: "new_qty",
        width: "15%",
        align: "center",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <><NumberFormat value={text} /> {row.base_unit_code} </>;
        }
      },
      {
        title: getLanguage({ id: "cost" }),
        dataIndex: "cost",
        width: "15%",
        align: "center",
        onCell: row => {
          if (!isNaN(row.index_no)) {
            if (this.props.status === 1 && this.props.cost_adjustment === 1) {
              return {
                record: row,
                dataIndex: "cost",
                editable: true,
                handleSave: this.handleSave,
                cost_adjustment: cost_adjustment,
                colSpan: 1,
                err_cost: this.props.err_cost
              };
            } else {
              return {
                record: row,
                dataIndex: "cost",
                colSpan: 1
              };
            }
          } else {
            return {
              colSpan: 0
            };
          }
        }
      },
      {
        title: getLanguage({ id: "subTotal" }),
        dataIndex: "sub_total",
        width: "15%",
        align: "center",
        render: text => <NumberFormat value={text} />
      },
      {
        title: "",
        dataIndex: "operation",
        width: "10%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 1
              }
            };
          } else if (!this.props.status || this.props.status === 1) {
            return (
              <div>
                <Tooltip
                  placement="top"
                  title={<IntlMessages id="detail" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceEdtBtn"
                    size="small"
                    onClick={() => this.handleItemDetailModal(row)}
                  >
                    <i className="ion-navicon-round" />
                  </Button>
                </Tooltip>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  onConfirm={() => this.handleDelete(row.index_no)}
                  okText={<IntlMessages id="yes" />}
                  cancelText={<IntlMessages id="no" />}
                >
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>
              </div>
            );
          } else {
            return (
              <Tooltip
                placement="top"
                title={<IntlMessages id="detail" />}
              >
                <Button
                  //style={{ borderRadius: "50%" }}
                  className="invoiceEdtBtn"
                  size="small"
                  onClick={() => this.handleItemDetailModal(row)}
                >
                  <i className="ion-navicon-round" />
                </Button>
              </Tooltip>
            );
          }
        }
      }
    ];

    const total = getTotal(dataSource);
    const info = [
      {
        index_no: getLanguage({ id: "totalItem" }),
        sub_total: parseFloat(this.state.count) - 1
      },
      {
        index_no: getLanguage({ id: "totalQuantity" }),
        sub_total: parseFloat(total.totalQty)
      },
      {
        index_no: getLanguage({ id: "totalCost" }),
        sub_total: parseFloat(total.totalAmount)
      }
    ];
    const modifiedData = dataSource.length > 0 ? [...dataSource, ...info] : [];

    return (
      <CardWrapper>
        <div>
          <ModalItemDetail
            visible={visible}
            selectedData={selectedItem}
            handleItemDetailModal={this.handleItemDetailModal}
            handleItemDetail={this.handleItemDetail}
            submit={this.handleItemDetail}
            departments={this.props.departments}
            projects={this.props.projects}
            status={this.props.status}
          />
          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns}
              pagination={false}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
