import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/inventory/issueReceipt/actions";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  DatePicker,
  Select,
  Divider,
  Spin,
  Row,
  Col,
  Empty,
  Radio
} from "antd";
import EditableTable from "./editableTable";
import ModalPassword from "./modalPassword";
import ModalAddAdjustmentType from "./modalAddAdjustmentType";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

const { getItems } = actions;

class IssueReceiptCreateForm extends Component {
  state = {
    id_location: null,
    item: null,
    items: [],
    itemDetails: {
      issue_receipt_details: []
    },
    cost_adjustment: 0,
    search: null,
    visible: false,
    visibleModalPassword: false,
    clear: false,
    err: false,
    err_cost: null,
    err_item: false
  };

  componentDidMount() {
    const { user } = this.props;
    const { getItems } = this.props;
    let { default_employee_location } = user ? user : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];

    if (default_employee_location.id_location) {
      getItems(default_employee_location.id_location);
    }
  }

  handleSubmit = password => {
    // e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { itemDetails } = this.state;

      // validasi item
      if (itemDetails.issue_receipt_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }

      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const data = Object.assign({}, password, values, itemDetails);
      // console.log(data);
      if (!err) {
        this.setState({ err: false });
        this.props.submit(data);
        this.handleModalPassword();
      }
    });
  };

  handleSelectLocation = id_location => {
    const { getItems } = this.props;
    this.setState({
      id_location: id_location,
      clear: this.state.id_location !== id_location ? true : false
    });

    getItems(id_location);
  };

  handleCostAdjustment = e => {
    this.setState({ cost_adjustment: e.target.value });
  };

  handleSelectAdjustmentType = id_adjustment_type => {
    if (id_adjustment_type) {
      const { adjustmentTypes } = this.props.issueReceipt;
      const { setFieldsValue } = this.props.form;

      const adjustmentType = adjustmentTypes.find(
        data => data.id_adjustment_type === id_adjustment_type
      );

      this.setState({ cost_adjustment: adjustmentType.cost_adjustment });
      setFieldsValue({
        id_account_adjustment: adjustmentType.id_account_adjustment,
        cost_adjustment: adjustmentType.cost_adjustment
      });
    }
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleModalPassword = () => {
    const { visibleModalPassword } = this.state;
    visibleModalPassword
      ? this.setState({ visibleModalPassword: false })
      : this.setState({ visibleModalPassword: true });
  };

  handleSelectItem = value => {
    const { items } = this.props.issueReceipt;
    const { search } = this.state;
    const { getFieldValue } = this.props.form;
    const id_location = getFieldValue("id_location");
    const { getItems } = this.props;
    // console.log(items.filter(item => item.id_item === value));
    this.setState({
      item: items.find(item => item.id_item === value)
    });

    if (search) {
      getItems(id_location);
      this.setState({ search: null });
    }
  };

  handleSelectModal = value => {
    const { transactions } = this.props.issueReceipt;
    const { setFieldsValue } = this.props.form;
    const transaction = transactions.find(
      curr_data => curr_data.issue_receipt_no === value
    );
    this.setState({
      items: transaction.issue_receipt_details,
      itemDetails: {
        issue_receipt_details: transaction.issue_receipt_details
      },
      clear: false
    });
    setFieldsValue({ issue_receipt_no: value });
    this.handleModal();
  };

  handleSearchItem = value => {
    const { getItems } = this.props;
    const { getFieldValue } = this.props.form;
    const id_location = getFieldValue("id_location");
    // console.log(this.props.purchaseInvoice);
    this.setState({ search: value });
    if (id_location) {
      getItems(id_location, value);
    }
  };

  handleChangeItem = value => {
    this.setState({ search: value });
  };

  handleSaveItemList = value => {
    this.setState({
      itemDetails: {
        issue_receipt_details: value
      },
      clear: false
    });
  };

  clearItem = value => {
    this.setState({
      item: null,
      itemDetails: {
        issue_receipt_details: value
      },
      clear: false
    });
  };

  clearItems = value => {
    this.setState({
      items: [],
      itemDetails: {
        issue_receipt_details: value
      },
      clear: false
    });
  };

  render() {
    // console.log(this.props.issueReceipt.transactions);
    const { search } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { issueReceipt } = this.props;
    const {
      items,
      locations,
      adjustmentTypes,
      accounts,
      departments,
      projects
    } = issueReceipt ? issueReceipt : [];
    const { user } = this.props;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingForeign
    } = issueReceipt;
    // const data = issueReceipt.issueReceipt ? issueReceipt.issueReceipt : [];

    // const used = data.is_used;
    const error = issueReceipt.errorField ? issueReceipt.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <>
        <ModalAddAdjustmentType
          handleModal={this.props.handleModalAddAdjustmentType}
          adjustmentType={this.props.adjustmentType}
          issueReceipt={this.props.issueReceipt}
          submit={this.props.handleSubmitAdjustmentType}
          visible={this.props.visibleAddAdjustmentType}
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <ModalPassword
            submit={this.handleSubmit}
            visible={this.state.visibleModalPassword}
            handleModal={this.handleModalPassword}
          />

          <Row>
            <Col span={12} style={{ paddingRight: "15px" }}>
              <InputGroup>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="issueReceipts.issueReceiptNo" />}
                  >
                    {getFieldDecorator("issue_receipt_no", {
                      // initialValue: data.issue_receipt_no,
                    })(<Input disabled={true} />)}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="issueReceipts.transactionDate" />}
                    style={{ marginBottom: 0, paddingBottom: 0 }}
                  >
                    {getFieldDecorator("transaction_date", {
                      initialValue: moment()
                    })(
                      <DatePicker
                        style={{ width: "100%" }}
                        showTime={{ format: "HH:mm:ss" }}
                        format="DD MMMM YYYY HH:mm:ss"
                        disabled={
                          !permissions.includes("Can Back Date") ? true : false
                        }
                        allowClear={false}
                        disabledDate={e => e.valueOf() > moment()}
                      />
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>

              <InputGroup>
                <Col span={18}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="issueReceipts.location" />}
                  >
                    {getFieldDecorator("id_location", {
                      initialValue: default_employee_location.id_location,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        showSearch
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="issueReceipts.location" />
                        }
                        loading={loadingForeign}
                        onSelect={this.handleSelectLocation}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={6}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="issueReceipts.costAdjustment" />}
                  >
                    {getFieldDecorator("cost_adjustment", {
                      initialValue: 0
                    })(
                      <Radio.Group
                        onChange={this.handleCostAdjustment}
                        // value={this.state.cost_adjustment}
                        buttonStyle="solid"
                        style={{ width: "100%" }}
                        disabled={true}
                      >
                        <Radio.Button
                          style={{ width: "50%", textAlign: "center" }}
                          value={0}
                        >
                          <IntlMessages id="no" />
                        </Radio.Button>
                        <Radio.Button
                          style={{ width: "50%", textAlign: "center" }}
                          value={1}
                        >
                          <IntlMessages id="yes" />
                        </Radio.Button>
                      </Radio.Group>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            </Col>
            <Col span={12} style={{ paddingLeft: "15px" }}>
              <Form.Item
                {...formTextArea}
                label={<IntlMessages id="issueReceipts.note" />}
              >
                {getFieldDecorator("note", {
                  // initialValue: data.note
                })(<TextArea autosize={{ minRows: 4 }} />)}
              </Form.Item>
            </Col>
          </Row>

          <Row>
            <Col span={12} style={{ paddingRight: "15px" }}>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="issueReceipts.adjustmentType" />}
                >
                  {getFieldDecorator("id_adjustment_type", {
                    // initialValue: data.id_adjustment_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="issueReceipts.adjustmentType" />
                      }
                      loading={loadingForeign}
                      onChange={this.handleSelectAdjustmentType}
                      optionFilterProp="children"
                      filterOption={true}
                    >
                      {permissions.includes("Create Adjustment Type") ? (
                        <Option
                          key={"Create Adjustment Type"}
                          value={undefined}
                          onClick={() =>
                            this.props.handleModalAddAdjustmentType()
                          }
                        >
                          <div
                            style={{
                              color: "#1890FF",
                              fontStyle: "italic"
                            }}
                          >
                            {" + " +
                              getLanguage({
                                id: "adjustmentTypes.pageHeader"
                              })}
                          </div>
                        </Option>
                      ) : null}

                      {adjustmentTypes.map((data, key) => (
                        <Option key={key} value={data.id_adjustment_type}>
                          {`${data.adjustment_type_code} (${data.description})`}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </Col>
            <Col span={12} style={{ paddingLeft: "15px" }}>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="issueReceipts.adjustmentAccount" />}
                >
                  {getFieldDecorator("id_account_adjustment", {
                    // initialValue: data.id_account_adjustment,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="issueReceipts.adjustmentAccount" />
                      }
                      loading={loadingForeign}
                      optionFilterProp="children"
                      filterOption={true}
                    >
                      {accounts.map((data, key) => (
                        <Option key={key} value={data.id_account}>
                          {`${data.account_code} ${data.account_name}`}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </Col>
          </Row>

          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="issueReceipts.subtitle.itemList" />
          </Divider>

          <Row>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                validateStatus={
                  error.issue_receipt_details || this.state.err_item
                    ? "error"
                    : null
                }
                help={
                  error.issue_receipt_details ? (
                    error.issue_receipt_details
                  ) : this.state.err_item ? (
                    <IntlMessages id="itemDataEmpty" />
                  ) : null
                }
                extra={<IntlMessages id="issueReceipts.item.help" />}
              >
                <Select
                  value={search ? search : []}
                  showSearch
                  allowClear
                  // optionFilterProp="children"
                  filterOption={false}
                  placeholder={
                    <IntlMessages id="issueReceipts.item.placeholder" />
                  }
                  loading={loadingItems}
                  notFoundContent={
                    loadingItems ? (
                      <Spin size="small" />
                    ) : getFieldValue("id_location") ? (
                      <Empty description={<IntlMessages id="noData" />} />
                    ) : (
                      <Empty
                        description={
                          <IntlMessages id="issueReceipts.item.noLocation" />
                        }
                      />
                    )
                  }
                  onSelect={this.handleSelectItem}
                  onSearch={this.handleSearchItem}
                  value={undefined}
                >
                  {items.map(data => (
                    <Option key={data.id_item} value={data.id_item}>
                      {data.item_code + " - " + data.item_name}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
          </Row>

          <Row>
            <Col span={24}>
              <EditableTable
                item={this.state.item}
                items={this.state.items}
                departments={departments}
                projects={projects}
                handleSaveItemList={this.handleSaveItemList}
                clearItem={this.clearItem}
                clearItems={this.clearItems}
                loadingItems={loadingItems}
                clear={this.state.clear}
                cost_adjustment={this.state.cost_adjustment}
                status={1}
                err_cost={this.state.err_cost}
              />
            </Col>
          </Row>

          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              style={{ marginTop: "50px" }}
              loading={loading || loadingSubmit}
              onClick={() => {
                this.props.form.validateFieldsAndScroll((err, values) => {
                  const { itemDetails } = this.state;

                  // validasi item
                  if (itemDetails.issue_receipt_details.length <= 0) {
                    return this.setState({ err_item: true });
                  } else {
                    this.setState({ err_item: false });
                  }

                  if (values.cost_adjustment) {
                    const find_cost = itemDetails.issue_receipt_details.find(
                      data => parseFloat(data.cost) <= 0
                    );

                    if (find_cost) {
                      this.setState({
                        err_cost: <IntlMessages id="zeroCost" />
                      });
                      return;
                    }
                  }

                  if (!err) {
                    this.setState({ err: false, err_cost: null });
                    return this.handleModalPassword();
                  } else {
                    this.setState({ err: true });
                    return;
                  }
                });
              }}
            >
              <IntlMessages id="button.submit" />
            </Button>

            <br />

            {this.state.err ? (
              <span style={{ color: "#f5222d" }}>
                <IntlMessages id="form.error" />
              </span>
            ) : null}

            {this.state.err_cost ? (
              <span style={{ color: "#f5222d" }}>{this.state.err_cost}</span>
            ) : null}

            {this.state.err_item ? (
              <IntlMessages id="itemDataEmpty">
                {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
              </IntlMessages>
            ) : null}
          </Form.Item>
        </Form>
      </>
    );
  }
}

const WrappedIssueReceiptCreateForm = Form.create({
  name: "create_issue_receipt"
})(IssueReceiptCreateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, { getItems }))(
  WrappedIssueReceiptCreateForm
);
