import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import IssueReceiptCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/issueReceipt/actions";
import adjustmentTypeActions from "../../../../redux/item/adjustmentType/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const redirecTo = "/dashboard/inventories/issue-receipts";
const TabPane = Tabs.TabPane;
const { storeAdjustmentType } = adjustmentTypeActions;

class CreateIssueReceipt extends Component {
  state = {
    visibleAddAdjustmentType: false
  };

  componentDidMount() {
    const { createIssueReceipt } = this.props;
    createIssueReceipt();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.issueReceipt.error) {
      notification("error", this.props.issueReceipt.error);
      this.props.clearError();
    }

    if (
      this.props.issueReceipt.success &&
      this.props.issueReceipt.id_issue_receipt
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.issueReceipt.id_issue_receipt
      );
      // notification("success", this.props.issueReceipt.success);
      // this.props.clearSuccess();
    }

    // Adjustment Type
    if (this.props.adjustmentType.error) {
      notification("error", this.props.adjustmentType.error);
      this.props.clearError();
    }

    if (this.props.adjustmentType.success) {
      const { createIssueReceipt, clearSuccess } = this.props;
      notification("success", this.props.adjustmentType.success);
      this.handleModalAddAdjustmentType();
      clearSuccess();
      createIssueReceipt();
    }
  }

  handleModalAddAdjustmentType = () => {
    this.setState({
      visibleAddAdjustmentType: !this.state.visibleAddAdjustmentType
    });
  };

  handleSubmit = value => {
    const { storeIssueReceipt } = this.props;
    storeIssueReceipt(value);
  };

  handleSubmitAdjustmentType = value => {
    const { storeAdjustmentType } = this.props;
    storeAdjustmentType(value);
  };

  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="issueReceipts.button.add" />}>
            <Button onClick={() => this.props.history.replace(redirecTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              <IssueReceiptCreateForm
                submit={this.handleSubmit}
                issueReceipt={this.props.issueReceipt}
                // AdjustmentType
                adjustmentType={this.props.adjustmentType}
                visibleAddAdjustmentType={this.state.visibleAddAdjustmentType}
                handleModalAddAdjustmentType={this.handleModalAddAdjustmentType}
                handleSubmitAdjustmentType={this.handleSubmitAdjustmentType}
              />
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
              disabled
            />
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    issueReceipt: state.issueReceipt,
    adjustmentType: state.adjustmentType
  };
};

export default compose(
  connect(mapStateToProps, { ...actions, storeAdjustmentType })
)(CreateIssueReceipt);
