const actions = {
  getIssueReceipts: (page, search, filter) => ({
    type: "GET_ISSUE_RECEIPTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getItems: (id_location, search) => ({
    type: "GET_FILTER_ITEMS_IR_REQUEST",
    payload: { id_location: id_location, search: search }
  }),
  createIssueReceipt: () => ({
    type: "CREATE_ISSUE_RECEIPT_REQUEST"
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_TRANSACTIONS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storeIssueReceipt: data => ({
    type: "STORE_ISSUE_RECEIPT_REQUEST",
    payload: { body: data }
  }),
  editIssueReceipt: id_issue_receipt => ({
    type: "EDIT_ISSUE_RECEIPT_REQUEST",
    payload: { id_issue_receipt: id_issue_receipt }
  }),
  updateIssueReceipt: (id_issue_receipt, data) => ({
    type: "UPDATE_ISSUE_RECEIPT_REQUEST",
    payload: {
      id_issue_receipt: id_issue_receipt,
      body: data
    }
  }),
  showIssueReceipt: id_issue_receipt => ({
    type: "SHOW_ISSUE_RECEIPT_REQUEST",
    payload: { id_issue_receipt: id_issue_receipt }
  }),
  confirmIssueReceipt: (id_issue_receipt, data) => ({
    type: "CONFIRM_ISSUE_RECEIPT_REQUEST",
    payload: { id_issue_receipt: id_issue_receipt, body: data }
  }),
  cancelIssueReceipt: (id_issue_receipt, data) => ({
    type: "CANCEL_ISSUE_RECEIPT_REQUEST",
    payload: { id_issue_receipt: id_issue_receipt, body: data }
  }),
  uploadDocument: (id_issue_receipt, data) => ({
    type: "UPLOAD_ISSUE_RECEIPT_DOCUMENT_REQUEST",
    payload: {
      id_issue_receipt,
      body: data
    }
  }),
  downloadDocument: id_issue_receipt_document => ({
    type: "DOWNLOAD_ISSUE_RECEIPT_DOCUMENT_REQUEST",
    payload: { id_issue_receipt_document }
  }),
  deleteDocument: (id_issue_receipt_document, id_issue_receipt) => ({
    type: "DELETE_ISSUE_RECEIPT_DOCUMENT_REQUEST",
    payload: {
      id_issue_receipt_document: id_issue_receipt_document,
      id_issue_receipt: id_issue_receipt
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
